import { createGlobalStyle } from "styled-components";


export const GlobalStyle= createGlobalStyle`
:root{
    --maxWidth: 1280px;
    --white:#FFF;
    --lightGrey:#eee;
    --medGrey:#353535;
    --darkGrey:#1c1c1c;
    --fontSuperBig:2.5rem;
    --fontBig:1.5rem;
    --fontMed:1.2rem;
    --fontSmall:1rem;

    overflow: hidden;
    position: fixed;
}


*{
    box-sizing:border-box;
    font-family: 'Work Sans';
    text-align: center;


}

h1{
    font-size:2rem;
    font-weight: 600;
    color:var(--white);
}
h3{
    font-size: 1.1rem;
    font-weight: 600;
}
p{
    font-size: 1rem;
    color:var(--white);
}


`